import React, { Component } from 'react';
import Message from './Message';
import '../styles/chatbot.css';

// ...existing imports...
class Chatbot extends Component {
  state = {
    messages: [],
    input: '',
    loading: false
  };

  handleInputChange = (event) => {
    this.setState({ input: event.target.value });
  };

  handleSendMessage = () => {
    const { input, messages } = this.state;
    if (input.trim()) {
      this.setState({
        messages: [...messages, { content: input, sender: 'user' }],
        input: '',
        loading: true
      }, this.handleBotResponse);
    }
  };

  handleBotResponse = async () => {
    const { messages } = this.state;
    const userMessage = messages[messages.length - 1].content;
    try {
      const response = await fetch('https://api-inference.huggingface.co/models/google/flan-t5-base', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ inputs: userMessage })
      });
      const data = await response.json();
      let botMessage = 'Sorry, I could not get an answer.';
      if (Array.isArray(data) && data[0]?.generated_text) {
        botMessage = data[0].generated_text;
      } else if (data.generated_text) {
        botMessage = data.generated_text;
      }
      this.setState({
        messages: [...this.state.messages, { content: botMessage, sender: 'bot' }],
        loading: false
      });
    } catch (e) {
      this.setState({
        messages: [...this.state.messages, { content: 'Error fetching answer.', sender: 'bot' }],
        loading: false
      });
    }
  };

  // ...render method remains the same, but add a loading indicator if you want...
}

export default Chatbot;